import React from "react";
import cx from "classnames";
import styles from "./Header.module.scss";
import "../../styles/_variables.scss";

type HeaderTheme = {
  backgroundColor?: string;
  color?: string;
  accentColor?: string;
};

type HeaderProps = {
  theme: HeaderTheme;
};

const Header = ({ theme }: HeaderProps) => {
  const classes = cx(styles.header);

  // Using Record<string, string> to avoid TypeScript error
  const rootStyle: Record<string, string> = {
    "--accent-color": theme.accentColor || "", // Provide a fallback value
  };

  const themeStyle = {
    backgroundColor: theme.backgroundColor,
    color: theme.color,
  };

  return (
    <div style={rootStyle as React.CSSProperties}>
      {" "}
      {/* Casting to React.CSSProperties */}
      <header
        className={classes}
        style={themeStyle as React.CSSProperties}
      ></header>
    </div>
  );
};

export default Header;
